.favorite-hero {
  margin-bottom: 80px;
  .inner {
    background: #f8f8f8;
    border-radius: 20px;
    position: relative;
    padding: clamp(15px, 3vw, 30px);
  }

  .content {
    display: flex;
    flex-direction: column;
    @media (max-width: 1200px) {
      max-width: 600px;
    }
    .breadcrumbs {
      margin-bottom: clamp(40px, 4.5vw, 70px);
    }
  }

  .image {
    position: absolute;
    bottom: 0;
    right: 50px;
    max-width: 325px;
    img {
      display: block;
    }
    @media (max-width: 992px) {
      display: none;
    }
  }
}

.offer-favorite {
  background: transparent;
  padding: 0;
  h2 {
    font-size: clamp(22px, 3vw, 30px);
  }
}

.viewers-items {
  margin-bottom: 100px;

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(22px, 3vw, 30px);
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);
    margin: 0;
    margin-bottom: 30px;
  }

  &__items {
    display: flex;
    gap: 20px;
    overflow: auto;

    .item {
      flex-shrink: 0;
      max-width: 285px;
      width: 100%;
      display: flex;
      border-radius: 10px;
      align-items: center;
      background: #f8f8f8;
      padding: 10px;
      gap: 10px;
      .content {
        p {
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 18px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: var(--gray);
          margin: 0;
          margin-bottom: 20px;
        }
        span {
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 14px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: var(--accent);
        }
      }
    }
  }
}
